// src/features/line-dashboard/settings/DroneEarlyInformSettings.jsx
"use client"

import * as React from "react"
import { Plus, RefreshCcw, Save, Trash2 } from "lucide-react"
import { toast } from "sonner"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"

const ENDPOINT = "/api/drone-early-inform"

function sortRecords(records) {
  return [...records].sort((a, b) => {
    if (a.lineId < b.lineId) return -1
    if (a.lineId > b.lineId) return 1
    if (a.mainStep < b.mainStep) return -1
    if (a.mainStep > b.mainStep) return 1
    return (a.id ?? 0) - (b.id ?? 0)
  })
}

export function DroneEarlyInformSettings({ lineId }) {
  const [state, setState] = React.useState({ records: [], isLoading: false, error: null })
  const [reloadKey, setReloadKey] = React.useState(0)
  const [formValues, setFormValues] = React.useState({
    lineId: lineId ?? "",
    mainStep: "",
    customEndStep: "",
  })
  const [isSubmitting, setIsSubmitting] = React.useState(false)
  const [editingId, setEditingId] = React.useState(null)
  const [editingValues, setEditingValues] = React.useState({ mainStep: "", customEndStep: "" })
  const [savingId, setSavingId] = React.useState(null)
  const [deletingId, setDeletingId] = React.useState(null)

  const effectiveLineId = React.useMemo(() => (Array.isArray(lineId) ? lineId[0] : lineId) ?? "", [lineId])

  React.useEffect(() => {
    setFormValues((previous) => ({
      ...previous,
      lineId: effectiveLineId,
    }))
  }, [effectiveLineId])

  React.useEffect(() => {
    const controller = new AbortController()

    async function load() {
      if (!effectiveLineId) {
        setState({ records: [], isLoading: false, error: null })
        return
      }

      setState((previous) => ({ ...previous, isLoading: true, error: null }))

      try {
        const params = new URLSearchParams({ lineId: String(effectiveLineId) })
        const response = await fetch(`${ENDPOINT}?${params.toString()}`, {
          signal: controller.signal,
          cache: "no-store",
        })

        if (!response.ok) {
          const payload = await response.json().catch(() => ({}))
          const message = typeof payload?.error === "string" ? payload.error : "데이터를 불러오지 못했습니다."
          throw new Error(message)
        }

        const payload = await response.json()
        const records = Array.isArray(payload?.records) ? payload.records : []
        setState({ records: sortRecords(records), isLoading: false, error: null })
      } catch (error) {
        if (controller.signal.aborted) return
        const message = error instanceof Error ? error.message : "데이터를 불러오지 못했습니다."
        setState({ records: [], isLoading: false, error: message })
      }
    }

    void load()

    return () => controller.abort()
  }, [effectiveLineId, reloadKey])

  const handleFormChange = (event) => {
    const { name, value } = event.target
    setFormValues((previous) => ({ ...previous, [name]: value }))
  }

  const resetForm = React.useCallback(() => {
    setFormValues({ lineId: effectiveLineId, mainStep: "", customEndStep: "" })
  }, [effectiveLineId])

  const triggerReload = React.useCallback(() => {
    setReloadKey((value) => value + 1)
  }, [])

  const handleSubmit = async (event) => {
    event.preventDefault()
    if (!effectiveLineId) {
      toast.error("lineId가 비어있습니다. URL을 확인해주세요.")
      return
    }

    const payload = {
      lineId: formValues.lineId || effectiveLineId,
      mainStep: formValues.mainStep,
      customEndStep: formValues.customEndStep,
    }

    if (!payload.mainStep.trim()) {
      toast.error("main_step 값을 입력해주세요.")
      return
    }

    setIsSubmitting(true)
    try {
      const response = await fetch(ENDPOINT, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      })

      const result = await response.json().catch(() => ({}))

      if (!response.ok) {
        const message = typeof result?.error === "string" ? result.error : "레코드 추가에 실패했습니다."
        throw new Error(message)
      }

      toast.success("레코드가 추가되었습니다.")
      resetForm()
      triggerReload()
    } catch (error) {
      const message = error instanceof Error ? error.message : "레코드 추가에 실패했습니다."
      toast.error(message)
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleRefresh = () => {
    triggerReload()
  }

  const handleStartEdit = (record) => {
    setEditingId(record.id)
    setEditingValues({
      mainStep: record.mainStep ?? "",
      customEndStep: record.customEndStep ?? "",
    })
  }

  const handleCancelEdit = () => {
    setEditingId(null)
    setEditingValues({ mainStep: "", customEndStep: "" })
    setSavingId(null)
  }

  const handleSaveEdit = async () => {
    if (!editingId) return

    const payload = {
      id: editingId,
      mainStep: editingValues.mainStep,
      customEndStep: editingValues.customEndStep,
    }

    if (!payload.mainStep.trim()) {
      toast.error("main_step 값을 입력해주세요.")
      return
    }

    setSavingId(editingId)
    try {
      const response = await fetch(ENDPOINT, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      })

      const result = await response.json().catch(() => ({}))

      if (!response.ok) {
        const message = typeof result?.error === "string" ? result.error : "레코드 수정에 실패했습니다."
        throw new Error(message)
      }

      toast.success("레코드가 저장되었습니다.")
      handleCancelEdit()
      triggerReload()
    } catch (error) {
      const message = error instanceof Error ? error.message : "레코드 수정에 실패했습니다."
      toast.error(message)
      setSavingId(null)
    }
  }

  const handleDelete = async (recordId) => {
    if (!recordId) return
    if (!window.confirm("정말 삭제하시겠습니까?")) {
      return
    }

    setDeletingId(recordId)
    try {
      const response = await fetch(ENDPOINT, {
        method: "DELETE",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ id: recordId }),
      })

      const result = await response.json().catch(() => ({}))

      if (!response.ok) {
        const message = typeof result?.error === "string" ? result.error : "레코드 삭제에 실패했습니다."
        throw new Error(message)
      }

      toast.success("레코드가 삭제되었습니다.")
      triggerReload()
    } catch (error) {
      const message = error instanceof Error ? error.message : "레코드 삭제에 실패했습니다."
      toast.error(message)
    } finally {
      setDeletingId(null)
    }
  }

  const renderTableBody = () => {
    if (state.isLoading) {
      return (
        <TableRow>
          <TableCell colSpan={5} className="text-center text-sm text-muted-foreground">
            불러오는 중...
          </TableCell>
        </TableRow>
      )
    }

    if (state.error) {
      return (
        <TableRow>
          <TableCell colSpan={5} className="text-center text-sm text-destructive">
            {state.error}
          </TableCell>
        </TableRow>
      )
    }

    if (!state.records.length) {
      return (
        <TableRow>
          <TableCell colSpan={5} className="text-center text-sm text-muted-foreground">
            등록된 레코드가 없습니다.
          </TableCell>
        </TableRow>
      )
    }

    return state.records.map((record) => {
      const isEditing = editingId === record.id
      return (
        <TableRow key={record.id ?? `placeholder-${record.mainStep}`}>
          <TableCell className="font-mono text-xs text-muted-foreground">{record.id}</TableCell>
          <TableCell>{record.lineId}</TableCell>
          <TableCell className="w-48">
            {isEditing ? (
              <Input
                name="mainStep"
                value={editingValues.mainStep}
                onChange={(event) =>
                  setEditingValues((previous) => ({ ...previous, mainStep: event.target.value }))
                }
                placeholder="메인 스텝"
              />
            ) : (
              record.mainStep
            )}
          </TableCell>
          <TableCell className="w-56">
            {isEditing ? (
              <Input
                name="customEndStep"
                value={editingValues.customEndStep}
                onChange={(event) =>
                  setEditingValues((previous) => ({ ...previous, customEndStep: event.target.value }))
                }
                placeholder="커스텀 종료 스텝"
              />
            ) : (
              record.customEndStep ?? "-"
            )}
          </TableCell>
          <TableCell className="w-40">
            {isEditing ? (
              <div className="flex items-center gap-2">
                <Button
                  type="button"
                  size="sm"
                  onClick={handleSaveEdit}
                  disabled={savingId === record.id}
                >
                  <Save className="h-4 w-4" /> 저장
                </Button>
                <Button type="button" size="sm" variant="ghost" onClick={handleCancelEdit}>
                  취소
                </Button>
              </div>
            ) : (
              <div className="flex items-center gap-2">
                <Button type="button" size="sm" variant="outline" onClick={() => handleStartEdit(record)}>
                  수정
                </Button>
                <Button
                  type="button"
                  size="sm"
                  variant="destructive"
                  onClick={() => handleDelete(record.id)}
                  disabled={deletingId === record.id}
                >
                  <Trash2 className="h-4 w-4" /> 삭제
                </Button>
              </div>
            )}
          </TableCell>
        </TableRow>
      )
    })
  }

  return (
    <div className="flex flex-col gap-6">
      <div className="rounded-xl border bg-card p-6 shadow-sm">
        <div className="flex flex-col gap-4">
          <div>
            <h1 className="text-xl font-semibold">드론 사전 통보 설정</h1>
            <p className="text-sm text-muted-foreground">
              drone_early_inform_v3 테이블에서 {effectiveLineId || "선택된"} 라인의 메인 스텝과 커스텀 종료 스텝을 관리합니다.
            </p>
          </div>

          <form onSubmit={handleSubmit} className="grid gap-3 rounded-lg border p-4 sm:grid-cols-4 sm:items-end">
            <div className="sm:col-span-1">
              <label className="mb-1 block text-xs font-medium uppercase text-muted-foreground">Line ID</label>
              <Input
                name="lineId"
                value={formValues.lineId}
                onChange={handleFormChange}
                placeholder="라인 ID"
                required
              />
            </div>
            <div className="sm:col-span-1">
              <label className="mb-1 block text-xs font-medium uppercase text-muted-foreground">Main Step</label>
              <Input
                name="mainStep"
                value={formValues.mainStep}
                onChange={handleFormChange}
                placeholder="메인 스텝"
                required
              />
            </div>
            <div className="sm:col-span-1">
              <label className="mb-1 block text-xs font-medium uppercase text-muted-foreground">Custom End Step</label>
              <Input
                name="customEndStep"
                value={formValues.customEndStep}
                onChange={handleFormChange}
                placeholder="커스텀 종료 스텝"
              />
            </div>
            <div className="sm:col-span-1 flex items-center gap-2">
              <Button type="submit" className="w-full" disabled={isSubmitting}>
                <Plus className="h-4 w-4" /> 추가
              </Button>
              <Button type="button" variant="outline" onClick={handleRefresh} disabled={state.isLoading}>
                <RefreshCcw className="h-4 w-4" /> 새로고침
              </Button>
            </div>
          </form>

          <TableContainer className="rounded-lg border">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead className="w-20">ID</TableHead>
                  <TableHead>Line ID</TableHead>
                  <TableHead>Main Step</TableHead>
                  <TableHead>Custom End Step</TableHead>
                  <TableHead className="w-40">Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>{renderTableBody()}</TableBody>
            </Table>
          </TableContainer>
        </div>
      </div>
    </div>
  )
}
